@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Oswald:wght@500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{width:100%;display:flex;align-items:center;height:100px}.header a{margin:0 28px;text-decoration:none;color:#181818;font-size:12px;font-weight:600;font-family:'Raleway', sans-serif}.container{position:relative;color:#181818;width:1280px;min-width:1280px;margin:0 auto;display:flex;justify-content:center;color:white}.container .page{position:absolute}.container .page .inner{display:flex;justify-content:center;height:70vh}.container .page .inner h1{font-family:'Oswald', sans-serif;font-size:61px;font-weight:800;letter-spacing:0.08rem}.container .page .inner h1 .line-wrap{overflow:hidden;height:86px}.container .page .inner p{font-family:'Open Sans', sans-serif;margin-top:200px;width:340px;font-weight:300;line-height:27px;font-size:16px}.container .page .inner button{margin-top:20px;width:170px;height:50px;background:none;font-family:'Roboto Mono', monospace;font-size:14px;cursor:pointer}.container .page .inner button:hover{background-color:white;color:#1b1b1c;height:55px}.container .page .inner .black{background-color:#1b1b1c;color:white}.page-enter{opacity:0}.page-enter-active{opacity:1;transition:opacity 400ms;transition-delay:600ms}.page-exit{opacity:1}.page-exit-active{opacity:0;transition:opacity 400ms}.inner{background-size:cover;background-position:center;padding:40px;box-shadow:inset 0 0 0 100vw rgba(0,0,0,0.4);min-width:70vw}.about{background-image:url(/home/static/media/3.476a3ff5.jpg)}.home{background-image:url(/home/static/media/2.c85c6903.jpg)}.contact{background-image:url(/home/static/media/1.689526ae.jpg)}

