@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Oswald:wght@500;600;700&display=swap');

.header {
  width: 100%;
  display: flex;
  align-items: center;
  height: 100px;
  a {
    margin: 0 28px;
    text-decoration: none;
    color: #181818;
    font-size: 12px;
    font-weight: 600;
    font-family: 'Raleway', sans-serif;
  }
}

.container {
  position: relative;
  color: #181818;
  width: 1280px;
  min-width: 1280px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  color: white;
  .page {
    position: absolute;
    .inner {
      display: flex;
      justify-content: center;
      height: 70vh;
      h1 {
        font-family: 'Oswald', sans-serif;
        font-size: 61px;
        font-weight: 800;
        letter-spacing: 0.08rem;
        .line-wrap {
          overflow: hidden;
          height: 86px;
        }
      }
      p {
        font-family: 'Open Sans', sans-serif;
        margin-top: 200px;
        width: 340px;
        font-weight: 300;
        line-height: 27px;
        font-size: 16px;     
      }
      button{
        margin-top: 20px;
        width: 170px;
        height: 50px;
        background: none;
        font-family: 'Roboto Mono', monospace;
        font-size: 14px;
        cursor: pointer;
      }
      button:hover{
        background-color: white;
        color: #1b1b1c;
        height: 55px;
      }
      .black{
        background-color: #1b1b1c;
        color: white;
      }
    }
  }
}

.page-enter {
  opacity: 0;
}
.page-enter-active {
  opacity: 1;
  transition: opacity 400ms;
  transition-delay: 600ms;
}
.page-exit {
  opacity: 1;
}
.page-exit-active {
  opacity: 0;
  transition: opacity 400ms;
}

.inner{
  background-size: cover;
  background-position: center;
  padding: 40px;
  box-shadow: inset 0 0 0 100vw rgba(0,0,0,0.4);
  min-width: 70vw;
}
.about{
  background-image: url('./assets/3.jpg');
}
.home{
  background-image: url('./assets/2.jpg');
}
.contact{
  background-image: url('./assets/1.jpg');
}